.we-content-list{height:45px;padding:20px;border-bottom:1px solid #ddd;position:relative}.we-content-list:hover{cursor:pointer;background:-webkit-linear-gradient(top, #fff, #EEE);background:linear-gradient(to bottom, #fff, #EEE)}.we-content-list dt img{border-radius:50%;height:45px;widht:45px}.we-content-list dd{line-height:22px;margin-left:15px;width:150px;color:#999;font-size:12px}.we-content-list .title{color:#333;font-size:14px}.we-content-list .context-menu{position:absolute;z-index:9999;height:38px;width:70px;cursor:pointer;display:none}.we-content-list .context-menu ul{position:absolute;left:0;z-index:1000;float:left;padding:5px 0;margin:2px 0 0;list-style:none;background-color:#ffffff;border:1px solid #ccc;border:1px solid rgba(0,0,0,0.2);border-radius:6px;box-shadow:0 5px 10px rgba(0,0,0,0.2);background-clip:padding-box}.we-content-list .context-menu li{line-height:20px}.we-content-list .context-menu a{display:block;padding:3px 20px;clear:both;font-weight:normal;line-height:20px;color:#333333;white-space:nowrap}



